.contacts-container {
  width: 320px;
  padding: 60px 20px 0;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 16px;
    padding-top: 80px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding-top: 60px;
  }
}

.contacts {
  position: relative;
  background-image: linear-gradient(to bottom,
      var(--dominant-color) 0%,
      var(--dominant-color) 25%,
      var(--background-color-pink) 25%,
      var(--background-color-pink) 100%);

  @media screen and (min-width: 768px) {
    background-image: linear-gradient(to bottom,
        var(--dominant-color) 0%,
        var(--dominant-color) 61%,
        var(--background-color-pink) 61%,
        var(--background-color-pink) 100%);
  }

  @media screen and (min-width: 1200px) {
    background-image: linear-gradient(to bottom,
        var(--dominant-color) 0%,
        var(--dominant-color) 27%,
        var(--background-color-pink) 27%,
        var(--background-color-pink) 100%);
  }
}

.contacts-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  // margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 32px;
    margin-bottom: 60px;
  }
}

.contacts-card {
  padding: 20px 12px;
  background-color: var(--dominant-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    box-shadow: none;
  }
}

.contacts-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 15px;
    justify-content: center;
  }

  @media screen and (min-width: 768px) {
    gap: 12px;
  }
}

.contacts-btn--light {
  display: block;
  height: 44px;
  width: 162px;
  font-weight: 700;
  font-size: 16px;
  line-height: calc(21px / 16px);
  text-align: center;
  border: none;
  border-radius: 22px;
  background-color: var(--dominant-color);
  color: var(--accent-color);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: var(--button-hover-color);
  }

  @media screen and (min-width: 768px) {
    width: 174px;
  }

  @media screen and (min-width: 1200px) {
    width: 168px;
  }
}

.contacts-btn--accent {
  display: block;
  height: 44px;
  width: 162px;
  font-weight: 700;
  font-size: 16px;
  line-height: calc(21px / 16px);
  text-align: center;
  border: none;
  border-radius: 22px;
  background-color: var(--accent-color);
  color: var(--dominant-color);
  cursor: pointer;
  margin-left: 15px;

  @media screen and (min-width: 768px) {
    width: 192px;
  }

  @media screen and (min-width: 1200px) {
    width: 198px;
  }

  &:hover,
  &:focus {
    color: #443c3e;
  }
}

.contacts-btn--svg-dark {
  margin-left: 8px;

  @media screen and (min-width: 768px) {
    margin-left: 13px;
  }
}

.contacts-btn--svg-light {
  margin-left: 13px;
}

// modal-style
.overlay {
  /* Скрываем подложку  */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

/* Стили для модальных окон */

.contacts-modal {
  /* Скрываем окна  */
  opacity: 0;
  visibility: hidden;

  /*  Установаем ширину окна  */
  width: 280px;
  max-width: 500px;

  /*  Центрируем и задаем z-index */
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30;
  /* Должен быть выше чем у подложки*/

  /*  Побочные стили   */
