/* Стили для активных классов подложки и окна */

.contacts-modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
  // background-image: url(../../src/images/traveling/fone-pgn1.png);
  background-color: rgb(253, 253, 253);
  width: 50%;
}


.modal__link {
  padding: 15px 20px;
  display: inline-block;
  background-color: var(--accent-color);
  margin: 10px;
  color: var(--dominant-color);
  border-radius: 15px;
  width: 220px;
  font-weight: 700;
  font-size: 14px;
  line-height: calc(21px / 16px);
  text-align: center;
  text-transform: uppercase;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    width: 250px;
    padding: 15px 40px;
  }

  @media screen and (min-width: 1200px) {
    width: 350px;
  }

  &:hover,
  &:focus {
    background-color: var(--accent-secondary-color);
    color: var(--secondary-color);
  }
}

.modal__title {
  font-family: var(--secondary-font);
  color: var(--accent-color);
  text-align: center;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.17;
  margin-bottom: 20px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    font-weight: 500;
    font-size: 30px;
    line-height: 1.25;
  }

  @media screen and (min-width: 1200px) {
    font-weight: 500;
    font-size: 34px;
    line-height: 1.45;
    margin-bottom: 25px;
  }

  &:hover,
  &:focus {
    color: #443c3e;
  }
}

.location-list {
  list-style: none;
}