@import '~node_modules/modern-normalize/modern-normalize.css';

:root {
  // ==========  Fonts  ==========
  --dominant-font: 'DM Sans';
  --secondary-font: 'Titan One';
  --font-second: 'Rubik';

  // ==========  Fonts-color  ==========
  --dominant-color: #ffffff;
  --secondary-color: #000000;
  --grey-color: #5c5050;

  // ==========  Modal-window background color  ==========
  --modal-window-color: #e17992;

  // ==========  Accent-color  ==========
  --accent-color: #55b89a;
  --accent-secondary-color: #443c3e;

  // ==========  Background-section color  ==========
  --background-color-white: #ffffff;
  --background-color-pink: #fff5f6;

  // ==========  Background-section-Hero + background-sectio-Products + color-section-Contacts  ==========
  --item-pink-color: #ffb8ca;
  --item-coffee-color: #f0d1a5;
  --item-green-color: #c2e297;

  // ==========  Button hover  ==========
  --button-hover-color: #ecebeb;

  // ==========  Border color  ==========
  --border-color: #e1e1e1;
  --menu--border-color: #E18298;

  //   --main-transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  font-family: var(--dominant-font), sans-serif;
  color: var(--dominant-color);
  letter-spacing: 0.04em;
  background-color: rgba(146, 207, 189, 0.4);
  // background-image: url('../images/traveling/beach.jpg');
  font-size: 14px;
  font-weight: 500;








}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list {
  list-style: none;
  margin: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  padding: 0px 20px;
  margin: 0 auto;


  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0px 32px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding: 0px 16px;
  }

}


.section {
  width: 100vw;
  // padding-top: 60px;
  padding-bottom: 60px;

  // @media screen and (min-width: 1200px) {
  //   padding-top: 94px;
  //   padding-bottom: 94px;
  // }
}

.link-decor {
  text-decoration: none;
}

.section-hero {
  font-family: var(--secondary-font);
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  text-transform: uppercase;


  // color: #55b89a;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  }
}

h1,
h2,
p {
  margin: 0;
  padding: 0;
}

button {
  padding: 0;
}

.button__main {
  display: block;

  padding-left: 15px;
  padding-right: 15px;
  height: 20px;

  border: none;
  border-radius: 22px;
  background-color: var(--dominant-color);

  font-weight: 700;
  font-size: 9px;
  line-height: 1.31;

  color: var(--grey-color);

  cursor: pointer;

  &:hover,
  &:focus {
    background-color: var(--accent-color);
    color: var(--background-color-pink);
  }

    
  
   @media screen and (min-width: 320px) {
      margin-left: 15px;
    }
  
    @media screen and (min-width: 768px) {
       padding-left: 15px;
        padding-right: 15px;
        height: 34px;
      
        border: none;
        border-radius: 22px;
        background-color: var(--dominant-color);
      
        font-weight: 700;
        font-size: 12px;
        line-height: 1.31;
      
        color: var(--grey-color);
        margin-left: 15px;
        margin-right: 75px;
        cursor: pointer;

        &:hover,
          &:focus {
            background-color: var(--accent-color);
            color: var(--background-color-pink);
          }
    }
  
    @media screen and (min-width: 1200px) {
      // margin-left: 15px;
      // padding-left: 15px;
      //   padding-right: 15px;
      //   height: 34px;
      
        border: none;
        border-radius: 22px;
        background-color: var(--dominant-color);
      
        font-weight: 700;
        font-size: 16px;
        line-height: 1.31;
      
        color: var(--grey-color);
        cursor: pointer;
        &:hover,
          &:focus {
            background-color: var(--accent-color);
            color: var(--background-color-pink);
          }
    }
}

.button--inverse {
  background-color: var(--accent-secondary-color);
  color: var(--background-color-pink);

  &:hover,
  &:focus {
    background-color: var(--accent-color);
  }
}