.switch {
  margin: 0 5px;
  display: inline-block;
  position: relative;
}

.switch > span {
  pointer-events: none;
  text-transform: uppercase;
  text-shadow: 0 1px rgba(0, 0, 0, .06);
  width: 50%;
  text-align: center;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 10px;
  font-weight: bold;
  position: absolute;
  top: 8px;
}

input.check-toggle-round-flat:checked ~ .off {
  color: #443c3e;
}

input.check-toggle-round-flat:checked ~ .on {
  color: #fff;
}

.switch > span.on {
  color: #443c3e;
  padding-left: 2px;
  left: 0;
}

.switch > span.off {
  color: #fff;
  padding-right: 4px;
  right: 0;
}

.check-toggle {
  visibility: hidden;
  margin-left: -99px;
  position: absolute;
}

.check-toggle + label {
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  outline: none;
  display: block;
  position: relative;
}

input.check-toggle-round-flat + label {
  width: 60px;
  height: 25px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
  border-radius: 30px;
  padding: 2px;
}

input.check-toggle-round-flat + label:before, input.check-toggle-round-flat + label:after {
  content: "";
  display: block;
  position: absolute;
}

input.check-toggle-round-flat + label:before {
  -ms-border-radius: 60px;
  -o-border-radius: 60px;
  background-color: #55b89a;
  border-radius: 60px;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
}

input.check-toggle-round-flat + label:after {
  width: 28px;
  -ms-border-radius: 52px;
  -o-border-radius: 52px;
  -o-transition: margin .2s;
  background-color: #fff;
  border-radius: 52px;
  transition: margin .2s;
  top: 4px;
  bottom: 4px;
  left: 4px;
}

input.check-toggle-round-flat:checked + label:after {
  margin-left: 24px;
}

