  margin-right: 20px;

}
.section__products {
  width: auto;

  @media screen and (min-width: 768px) {
      width: auto;
      margin-bottom: auto;
      margin-top: auto;
      margin-top: 50px;
      margin-bottom: 50px;
    }
  
    @media screen and (min-width: 1200px) {
      width: auto;
  
    }
}
.products__title {
  font-family: var(--dominant-font);
  font-size: 25px;
  line-height: 1.15;
  font-weight: 400;
  text-transform: uppercase;

  text-align: center;
  margin-top: 5px;
  margin-bottom: 20px;

  color: #5c5050;

  @media screen and (min-width: 768px) {
    font-size: 25px;
    margin-top: 18px;
    margin-bottom: 10px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 35px;
    line-height: 1.14;
    margin-top: 30px;

  }
}


